  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(135deg, #ff6b6b 0%, #4ecdc4 50%, #45b7d1 100%);
  min-height: 100vh;
  color: #333;
}

header {
  background: linear-gradient(135deg, rgba(255, 107, 107, 0.95) 0%, rgba(78, 205, 196, 0.95) 50%, rgba(69, 183, 209, 0.95) 100%);
  backdrop-filter: blur(15px);
  padding: 1.5rem 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  text-align: center;
  border-bottom: 3px solid rgba(255, 255, 255, 0.2);
}

header h1 {
  font-family: 'Caveat', cursive;
  font-size: 3rem;
  color: white;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.3);
  margin: 0;
  letter-spacing: 2px;
  font-weight: 700;
}

main {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 2rem;
  min-height: calc(100vh - 100px);
}

nav {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 1.5rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  height: fit-content;
}

.project-list {
  list-style: none;
}

.project-item {
  margin-bottom: 0.5rem;
}

.project-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.project-container .project-btn {
  flex: 1;
}

.delete-project-btn {
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(255, 107, 107, 0.3);
  opacity: 0.7;
}

.delete-project-btn:hover {
  background: linear-gradient(135deg, #ff8e8e, #ff6b6b);
  transform: scale(1.1);
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);
  opacity: 1;
}

.project-btn {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.9);
  border: 2px solid transparent;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  font-size: 0.9rem;
  color: #2d3748;
  font-weight: 500;
}

.project-btn:hover {
  background: rgba(255, 255, 255, 1);
  border-color: #ff6b6b;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.3);
}

.project-btn.active {
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  border-color: #ff6b6b;
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);
}

.add-project-btn {
  width: 100%;
  padding: 0.75rem;
  background: linear-gradient(135deg, #4ecdc4, #44a08d);
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
  font-weight: 600;
  box-shadow: 0 4px 12px rgba(78, 205, 196, 0.3);
}

.add-project-btn:hover {
  background: linear-gradient(135deg, #44a08d, #4ecdc4);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(78, 205, 196, 0.4);
}

#main-content {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.content-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e2e8f0;
}

.content-header h2 {
  font-size: 1.8rem;
  color: #2d3748;
}

.add-todo-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #45b7d1, #3a9bb8);
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  box-shadow: 0 4px 12px rgba(69, 183, 209, 0.3);
}

.add-todo-btn:hover {
  background: linear-gradient(135deg, #3a9bb8, #45b7d1);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(69, 183, 209, 0.4);
}

.todo-list {
  list-style: none;
}

.todo-item {
  background: rgba(255, 255, 255, 0.95);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  padding: 1.5rem;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
  position: relative;
  backdrop-filter: blur(10px);
}

.todo-item:hover {
  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.2);
  transform: translateY(-4px);
  border-color: rgba(255, 107, 107, 0.3);
}

.todo-item.completed {
  opacity: 0.7;
  background: #f0fff4;
  border-color: #9ae6b4;
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.todo-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #2d3748;
  margin-bottom: 0.5rem;
}

.todo-item.completed .todo-title {
  text-decoration: line-through;
  color: #718096;
}

.todo-priority {
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
}

.priority-high {
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  box-shadow: 0 2px 8px rgba(255, 107, 107, 0.3);
}

.priority-normal {
  background: linear-gradient(135deg, #ffd93d, #ffed4e);
  color: #2d3748;
  box-shadow: 0 2px 8px rgba(255, 217, 61, 0.3);
}

.priority-low {
  background: linear-gradient(135deg, #4ecdc4, #44a08d);
  color: white;
  box-shadow: 0 2px 8px rgba(78, 205, 196, 0.3);
}

.todo-description {
  color: #718096;
  margin-bottom: 1rem;
  line-height: 1.5;
}

.todo-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.875rem;
  color: #a0aec0;
}

.todo-due-date {
  font-weight: 500;
}

.todo-actions {
  display: flex;
  gap: 0.5rem;
}

.todo-btn {
  padding: 0.5rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.875rem;
}

.complete-btn {
  background: linear-gradient(135deg, #4ecdc4, #44a08d);
  color: white;
  box-shadow: 0 2px 8px rgba(78, 205, 196, 0.3);
}

.complete-btn:hover {
  background: linear-gradient(135deg, #44a08d, #4ecdc4);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(78, 205, 196, 0.4);
}

.delete-btn {
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  box-shadow: 0 2px 8px rgba(255, 107, 107, 0.3);
}

.delete-btn:hover {
  background: linear-gradient(135deg, #ff8e8e, #ff6b6b);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);
}

.edit-btn {
  background: linear-gradient(135deg, #45b7d1, #3a9bb8);
  color: white;
  box-shadow: 0 2px 8px rgba(69, 183, 209, 0.3);
}

.edit-btn:hover {
  background: linear-gradient(135deg, #3a9bb8, #45b7d1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(69, 183, 209, 0.4);
}

/* Dialog Styles */
dialog {
  border: none;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  background: white;
  max-width: 500px;
  width: 90%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
}

dialog::backdrop {
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
}

dialog form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 600;
  color: #2d3748;
}

.form-group input,
.form-group textarea {
  padding: 1rem 1.25rem;
  border: 3px solid #a0aec0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: white;
  box-sizing: border-box;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #4299e1;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.1);
  transform: translateY(-1px);
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

fieldset {
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  padding: 1rem;
}

fieldset legend {
  font-weight: 600;
  color: #2d3748;
  padding: 0 0.5rem;
}

.priority-options {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.priority-option {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.priority-option input[type="radio"] {
  margin: 0;
}

.dialog-buttons {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.cancel-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(255, 107, 107, 0.3);
}

.cancel-btn:hover {
  background: linear-gradient(135deg, #ff8e8e, #ff6b6b);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);
}

dialog input[type="submit"] {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #45b7d1, #3a9bb8);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(69, 183, 209, 0.3);
}

dialog input[type="submit"]:hover {
  background: linear-gradient(135deg, #3a9bb8, #45b7d1);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(69, 183, 209, 0.4);
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 3rem;
  color: #a0aec0;
}

.empty-state h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #718096;
}

.empty-state p {
  font-size: 1.1rem;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
  main {
    grid-template-columns: 1fr;
    padding: 1rem;
  }
  
  nav {
    order: 2;
  }
  
  #main-content {
    order: 1;
  }
  
  .content-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  
  .todo-header {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .todo-meta {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
}
