;\r\n  border-radius: 50%;\r\n  width: 24px;\r\n  height: 24px;\r\n  cursor: pointer;\r\n  font-size: 16px;\r\n  font-weight: bold;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  transition: all 0.3s ease;\r\n  box-shadow: 0 2px 8px rgba(255, 107, 107, 0.3);\r\n  opacity: 0.7;\r\n}\r\n\r\n.delete-project-btn:hover {\r\n  background: linear-gradient(135deg, #ff8e8e, #ff6b6b);\r\n  transform: scale(1.1);\r\n  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);\r\n  opacity: 1;\r\n}\r\n\r\n.project-btn {\r\n  width: 100%;\r\n  padding: 0.75rem 1rem;\r\n  background: rgba(255, 255, 255, 0.9);\r\n  border: 2px solid transparent;\r\n  border-radius: 12px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  text-align: left;\r\n  font-size: 0.9rem;\r\n  color: #2d3748;\r\n  font-weight: 500;\r\n}\r\n\r\n.project-btn:hover {\r\n  background: rgba(255, 255, 255, 1);\r\n  border-color: #ff6b6b;\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.3);\r\n}\r\n\r\n.project-btn.active {\r\n  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);\r\n  color: white;\r\n  border-color: #ff6b6b;\r\n  box-shadow: 0 4px 12px rgba(255, 107, 107, 0.4);\r\n}\r\n\r\n.add-project-btn {\r\n  width: 100%;\r\n  padding: 0.75rem;\r\n  background: linear-gradient(135deg, #4ecdc4, #44a08d);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 12px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  margin-top: 1rem;\r\n  font-weight: 600;\r\n  box-shadow: 0 4px 12px rgba(78, 205, 196, 0.3);\r\n}\r\n\r\n.add-project-btn:hover {\r\n  background: linear-gradient(135deg, #44a08d, #4ecdc4);\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 6px 16px rgba(78, 205, 196, 0.4);\r\n}\r\n\r\n#main-content {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  backdrop-filter: blur(10px);\r\n  border-radius: 15px;\r\n  padding: 2rem;\r\n  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.content-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 2rem;\r\n  padding-bottom: 1rem;\r\n  border-bottom: 2px solid #e2e8f0;\r\n}\r\n\r\n.content-header h2 {\r\n  font-size: 1.8rem;\r\n  color: #2d3748;\r\n}\r\n\r\n.add-todo-btn {\r\n  padding: 0.75rem 1.5rem;\r\n  background: linear-gradient(135deg, #45b7d1, #3a9bb8);\r\n  color: white;\r\n  border: none;\r\n  border-radius: 12px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease;\r\n  font-weight: 600;\r\n  box-shadow: 0 4px 12px rgba(69, 183, 209, 0.3);\r\n}\r\n\r\n.add-todo-btn:hover {\r\n  background: linear-gradient(135deg, #3a9bb8, #45b7d1);\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 6px 16px rgba(69, 183, 209, 0.4);\r\n}\r\n\r\n.todo-list {\r\n  list-style: none;\r\n}\r\n\r\n.todo-item {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  border: 2px solid rgba(255, 255, 255, 0.3);\r\n  border-radius: 16px;\r\n  padding: 1.5rem;\r\n  margin-bottom: 1rem;\r\n  transition: all 0.3s ease;\r\n  position: relative;\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.todo-item:hover {\r\n  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.2);\r\n  transform: translateY(-4px);\r\n  border-color: rgba(255, 107, 107, 0.3);\r\n}\r\n\r\n.todo-item.completed {\r\n  opacity: 0.7;\r\n  background: #f0fff4;\r\n  border-color: #9ae6b4;\r\n}\r\n\r\n.todo-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: flex-start;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.todo-title {\r\n  font-size: 1.2rem;\r\n  font-weight: 600;\r\n  color: #2d3748;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.todo-item.completed .todo-title {\r\n  text-decoration: line-through;\r\n  color: #718096;\r\n}\r\n\r\n.todo-priority {\r\n  padding: 0.25rem 0.75rem;\r\n  border-radius: 20px;\r\n  font-size: 0.75rem;\r\n  font-weight: 600;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.priority-high {\r\n  background: linear-gradient(135deg, #ff6b6b, #ff8e8e);\r\n  color: white;\r\n  box-shadow: 0 2px 8px 